@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap');

/* common */
:root {
	--lightBlue: #074B8F;
	--white: #fff;

	--darkBlue: #1E0B9B;

	--sliderBG: #E8E8E8;
	--black: #171717;
	--yellow: #F7CA01;
}

* {
	margin: 0;
	padding: 0;
}

.overlay {
	background: rgb(23, 23, 23);
	opacity: 0;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	-webkit-transition: opacity 0.2s ease-in-out;
	-o-transition: opacity 0.2s ease-in-out;
	transition: opacity 0.2s ease-in-out;
	z-index: -1;

}

.overlay.--active {
	opacity: .5;
	z-index: 2;
	-webkit-transition: opacity 0.2s ease-in-out;
	-o-transition: opacity 0.2s ease-in-out;
	transition: opacity 0.2s ease-in-out;

}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

img {
	width: 100%;
	height: auto;
}

body {
	font-family: 'Rubik', sans-serif;
	color: var(--black);

}

body.--blocked {
	overflow: hidden;
}



/* mobile */

a {
	text-decoration: none;
	font-weight: 400;
	font-size: 16px;
	line-height: 100%;
	letter-spacing: -0.03em;

}

p {
	font-size: 16px;
	line-height: 150%;
	letter-spacing: -0.03em;
}

.btn {
	font-family: 'Rubik', sans-serif;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	border: none;
	border-radius: 50px;
    background: var(--darkBlue);
	height: 50px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-weight: 500;
	font-size: 16px;
	line-height: 100%;
	cursor: pointer;
	-webkit-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
	width: 120px;
}



.logo {
	width: 260px;
	height: auto;
}



.slider {
    width: 100%;
    position: relative;
}
.slider-text {
    position: absolute;
    left: calc((100% - 1280px)/2);
    top: 40%;
    max-width: 40%;
    
    h3 {
        font-size: 60px;
        line-height: 100%;
        color: #13085f;
        span {
            font-size: 72px;
        }
    }
    p {
        width: 80%;
        margin: 30px 0;
    }
}


.about-us {
    display: flex;
    padding: 200px 0;
    
}
.about-us-capture {
    width: 50%;
height: auto;

    img {
        object-fit: cover;
    }
}
.about-us-text {
    width: 50%;
    padding-right: calc((100% - 1280px)/2);
    box-sizing: border-box;
}
.title {
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 30px;
    color: #1E0B9B;
    display: block;
	/* position: relative;
	padding-bottom: 5px; */
/* 	&::after {
		position: absolute;
		content: '';
		width: 15px;
		height: 2px;
		bottom: 0;
		left: 0;
		background: #1E0B9B;
	} */
}

.text-header {
    font-size: 48px;
    line-height: 120%;
    margin-bottom: 40px;
    color:  #13085f;
}

.list {
	font-size: 20px;
	line-height: 150%;
	margin: 30px 0;
	li {
		margin-bottom: 20px;
		padding-left: 40px;
		position: relative;
		display: block;
		
		&::before {
		content: '>';
		position: absolute;
		left: 0;
		top: 0;
	display: flex;
	justify-content: center;
	align-items: center;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		font-size: 20px;
		font-weight: 500;
		background: #294DA414 0% 0% no-repeat padding-box;
	}
	}
	
	
}

.pill {
	display: flex;
	box-sizing: border-box;
	padding: 100px 0;
	&.pl {
		padding-left: calc((100% - 1280px)/2);
		background: rgba(41, 77, 164, 0.0784313725) 0% 0% no-repeat padding-box;
	}
	&.pr {
		padding-right: calc((100% - 1280px)/2);
	}
}

.pill-image,
.pill-text  {
	width: 50%;
}
.pill-text {
	padding: 0 30px;
	box-sizing: border-box;
}
.pill-btn {
	text-decoration: underline;
}
.pharmacy-wrapper {
	padding: 200px 0;
	max-width: 1280px;
	margin: 0 auto;
	width: 100%;
}

.pharmacies {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.pharmacy {
	width: 100px;
	height: auto;
}
.map {
	width: 100%;
}

.header {
	margin: 0 auto;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
    align-items: center;
	padding: 10px 15px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
    background: #fff;


}

.navbar {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;


}




.tel-btn {
	width: 100%;
	height: 40px;
}

.nav-link {
	cursor: pointer;
    margin-right: 45px;
    color: var(--darkBlue);
    border-bottom: 2px solid transparent;
    padding-bottom: 2px;

    &:last-child {
        margin-right: 0;
    }
    &:hover {
        border-bottom: 2px solid var(--darkBlue);
    }
}
.nav-link svg {
	width: 100%;
	height: auto;
}

.nav-btn {
    border-radius: 50px;
    background: var(--darkBlue);
    color: #fff;
    font-size: 24px;
    line-height: 100%;
    width: 180px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.slider_title {
	font-weight: 500;
	font-size: 30px;
	line-height: 100%;
	letter-spacing: -0.03em;
	margin: 15px 0;
	text-align: center;
}

.slider_description {
	text-align: center;
}

.slider_text {
	margin-bottom: 30px;
}

.slider_photo {
	width: 100%;
	max-width: 750px;
}

.slider_img {

	border-radius: 5px;
	height: 100%;
}

.slider_btn {
	width: 160px;
	margin: 0 auto;
}
.slider_btn:hover,
.single_buy_btn:hover,
.ordering-submit:hover,
.product_detail_btn:hover {
	background: #FFDA35;
	-webkit-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}

.center {
	width: 100%;
	padding: 0 15px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.advantages_wrapper {
	padding: 30px 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;

}

.advantage {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	width: 100%;
	height: 60px;
	padding: 15px 17px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	background: var(--white);
	border-radius: 15px;
	margin-bottom: 5px;
}

.advantage:last-child {
