}

.advantage_title {
	color: var(--darkBlue);
	font-weight: 400;
	font-size: 16px;
	line-height: 100%;
	letter-spacing: -0.03em;
}

.main {
	padding: 40px 0;
	max-width: 1280px;
	margin: 0 auto;
	-webkit-box-sizing: border-box;
