}

.filter_wrapper .chosen a {
	color: var(--lightBlue);
}

.filter_wrapper a {
	font-size: 16px;
	line-height: 150%;
	color: var(--darkBlue);
}
.filter_wrapper a:hover {
	color: #171717;
}

