	line-height: 150%;
	letter-spacing: -0.03em;
	color: #C3C3C3;
	margin-left: 5px;
}

.ordering_wrapper {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: justify;
