	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}


.product {
	width: calc((100% - 5px)/2);
	height: auto;
	background: #FFFFFF;
	border-radius: 5px;
	padding: 10px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	margin-bottom: 5px;
	margin-right: 5px;
	-webkit-transition: opacity 0.2s ease-in-out;
	-o-transition: opacity 0.2s ease-in-out;
	transition: opacity 0.2s ease-in-out;
}

.product:hover .woocommerce-loop-product__title {
	text-decoration: underline;
}
.product:hover .product_detail_btn{
	opacity: 1;
}

.product:nth-child(2n) {
	margin-right: 0;
}

.woocommerce-loop-product__title {

	font-weight: 500;
	font-size: 16px;
	line-height: 100%;
	letter-spacing: -0.03em;

}

.product_info {
	margin-top: 10px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	min-height: 65px;
	height: auto;
}

.product_img {
	position: relative;
}


.product_detail_btn {
	opacity: 0;
	position: absolute;
	left: 50%;
	top: 55%;
	width: 90%;
	transform: translate(-50%,-50%);
	-webkit-transition: opacity 0.2s ease-in-out;
	-o-transition: opacity 0.2s ease-in-out;
	transition: opacity 0.2s ease-in-out;
}

.product .price {
	font-weight: 500;
	font-size: 16px;
	line-height: 100%;
	letter-spacing: -0.03em;
	color: var(--lightBlue);
	margin-top: auto;
}

.product .capacity {
	font-size: 12px;
	line-height: 150%;
	letter-spacing: -0.03em;
	color: var(--darkBlue);
}

.page-numbers {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}

.page-numbers {
