	flex-direction: column;
	margin-bottom: 30px;
}

.contact_title,
.socials_title {
	font-weight: 500;
	font-size: 20px;
	margin-bottom: 10px;
}

.contact_title {
	position: relative;
	padding-left: 25px;
}

.contact_title::before {
	content: url('https://ista.uz/assets/svg/pin.svg');
	position: absolute;
	width: 15px;
	height: 20px;
	left: 0;
}

.contact_link {
	display: block;
	color: inherit;
	font-size: 18px;
	font-weight: 400;
	margin-bottom: 10px;
	border-bottom: 2px solid transparent;
	width: fit-content;
	padding-bottom: 2px;
}

.contact_link:hover {
	border-color: #fff;
}

.contact_tel {
	font-weight: 500;
	font-size: 20px;
	margin-bottom: 5px;
}

.ft-socials {
	border-top: 2px solid var(--lightBlue);
